	module.exports = {"data":{"markdownRemark":{"html":"<p>HTML elements are written with a starting tag, an ending tag, and the content in between:</p>\n<ul>\n<li><code>&#x3C;tagname>content&#x3C;/tagname></code></li>\n</ul>\n<p>The HTML element is everything from the start tag to the end tag:</p>\n<ul>\n<li><code>&#x3C;p>My first HTML paragraph.&#x3C;/p></code></li>\n</ul>\n<p>Opening tags look like this: <code>&#x3C;h1></code>, and closing tags look like this: <code>&#x3C;/h1></code></p>\n<ul>\n<li>Note that the only difference between opening tags and closing tags, is that closing tags have a slash after their opening angle bracket. The slash in the closing tag is very important because it indicates that the tag is the end of the element.</li>\n</ul>","fields":{"slug":"/certificates/say-hello-to-html-elements/"},"frontmatter":{"title":"Say Hello to HTML Elements"}}},"pathContext":{"slug":"/certificates/say-hello-to-html-elements/"}}

